html.is-clipped--navbar {
  overflow-y: hidden;
}

.lisk-logo {
  display: block;
  width: 86px;
  height: 35px;
  background-image: url(../img/lisk-logo-light.svg);
  background-size: contain;
  background-repeat: no-repeat;
  margin-top: 25px;

}

body {
  padding-top: var(--height-navbar);
}

.navbar {
  background: linear-gradient(0deg,#0c152e,#101c3d);
  color: var(--color-navbar-text);
  font-size: 15px;
  height: var(--height-navbar);
  position: fixed;
  top: 0;
  width: 100%;
  word-wrap: break-word;
  z-index: var(--z-index-navbar);
  padding: 0px 20px 0px 20px;
}

.navbar-content {
  margin: auto;
  display: flex;
  max-width: 1200px;
}

.navbar a {
  text-decoration: none;
}

.navbar-brand .navbar-item:first-child,
.navbar-brand .navbar-item:first-child a {
  color: var(--color-navbar-text);
  font-size: 1.15rem;
}

.navbar-brand .separator {
  padding: 0 0.375rem;
}

@media screen and (min-width: 1200px) {
  .navbar-end > .navbar-item,
  .navbar-end .navbar-link {
    color: var(--color-navbar-text);
  }

  .navbar-end > a.navbar-item:hover,
  .navbar-end .navbar-link:hover {
    background-color: var(--color-navbar-bg-focus);
    color: var(--color-navbar-text);
  }

  .navbar-end .navbar-link::after {
    border-color: var(--color-navbar-text);
  }

  .navbar-item.has-dropdown:hover .navbar-link {
    background-color: var(--color-navbar-bg-focus);
    color: var(--color-navbar-text);
  }
}

.navbar-brand {
  align-items: stretch;
  display: flex;
  flex-shrink: 0;
  height: var(--height-navbar);
  flex: 1
}

.navbar-burger {
  color: var(--color-navbar-text);
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  display: block;
  height: var(--height-navbar);
  position: relative;
  /* width: var(--height-navbar); */
  width: 2.75rem;
  margin-left: auto;
  padding: 0;
}

.navbar-burger::-moz-focus-inner {
  border: none;
}

.navbar-burger span {
  background-color: var(--color-navbar-text);
  display: block;
  height: 1px;
  left: 50%;
  margin-left: -7px;
  position: absolute;
  top: 50%;
  width: 15px;
}

.navbar-burger span:nth-child(1) {
  margin-top: -6px;
}

.navbar-burger span:nth-child(2) {
  margin-top: -1px;
}

.navbar-burger span:nth-child(3) {
  margin-top: 4px;
}

.navbar-burger.is-active span:nth-child(1) {
  margin-left: -5px;
  transform: rotate(45deg);
  transform-origin: left top;
}

.navbar-burger.is-active span:nth-child(2) {
  opacity: 0;
}

.navbar-burger.is-active span:nth-child(3) {
  margin-left: -5px;
  transform: rotate(-45deg);
  transform-origin: left bottom;
}

.navbar-menu {
  display: none;
}

.navbar-item,
.navbar-link {
  color: var(--color-text);
  display: block;
  line-height: 1.5;
  padding: 0.5rem 1rem;
  position: relative;
}

.navbar-item {
  flex: none;
}

.navbar-item.has-dropdown {
  padding: 0;
}

.navbar-item .icon {
  width: 1.1rem;
  height: 1.1rem;
  display: block;
}

.navbar-link {
  padding-right: 2.5em;
}

.navbar-dropdown {
  font-size: 0.875rem;
}

.navbar-dropdown .navbar-item {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.navbar-divider {
  background-color: #e1e1e1;
  border: none;
  height: 1px;
  margin: 0.25rem 0;
}

@media screen and (max-width: 1023px) {
  .navbar-brand .navbar-item {
    align-items: center;
    display: flex;
  }

  .navbar-menu {
    background-color: #fff;
    padding: 0.5rem 0;
  }

  .navbar-menu.is-active {
    display: block;
    box-shadow: 0 8px 16px rgba(10, 10, 10, 0.1);
    max-height: var(--height-nav);
    overflow-y: auto;
  }

  .navbar-menu a.navbar-item:hover,
  .navbar-menu .navbar-link:hover {
    background-color: #fafafa;
  }
}

@media screen and (min-width: 1250px) {
  .navbar,
  .navbar-menu,
  .navbar-end {
    align-items: stretch;
    display: flex;
  }

  .navbar-burger {
    display: none;
  }

  .navbar-item,
  .navbar-link {
    align-items: center;
    display: flex;
  }

  .navbar-item.has-dropdown {
    align-items: stretch;
  }

  .navbar-item.is-hoverable:hover .navbar-dropdown {
    display: block;
  }

  .navbar-link::after {
    border-width: 0 0 1px 1px;
    border-style: solid;
    content: " ";
    display: block;
    height: 0.5em;
    pointer-events: none;
    position: absolute;
    transform: rotate(-45deg);
    width: 0.5em;
    margin-top: -0.375em;
    right: 1.125em;
    top: 50%;
  }

  .navbar-menu {
    flex-grow: 1;
  }

  .navbar-end {
    justify-content: flex-end;
    margin-left: 0;
  }

  .navbar-dropdown {
    background-color: #fff;
    border: 1px solid #e1e1e1;
    display: none;
    font-size: 0.875rem;
    top: 100%;
    left: 0;
    min-width: 100%;
    position: absolute;
  }

  .navbar-dropdown .navbar-item {
    padding: 0.5rem 1rem;
    white-space: nowrap;
  }

  .navbar-dropdown a.navbar-item {
    padding-right: 3rem;
  }

  .navbar-dropdown.is-right {
    left: auto;
    right: 0;
  }

  .navbar-dropdown a.navbar-item:hover {
    background-color: #fafafa;
  }
}

.navbar .button {
  display: inline-flex;
  align-items: center;
  background-color: transparent;
  border: 1px solid rgba(255,255,255,.36);
  border-radius: 3px;
  height: 1.75rem;
  color: #FFFFFF;
  padding: 0 0.75em;
  white-space: nowrap;
  margin-left: 13.59rem;
  font-size: 12px;
}

.nav-brand__name-area {
  display: flex;
  white-space: nowrap;
  align-items: center;
  margin-right: 127px;
  margin-left: 13.9px;
}

.nav-brand__name-area:before{
 content: "";
  display: flex;
  align-items: center;
  width: 1px;
  height: 15px;
  background: white;
  opacity:0.12;
  margin-right: 10px;
}

.nav-brand__name-area__title{
  opacity: 0.64;
}

@media screen and (max-width:800px) {
  .navbar{
    justify-content: space-between;
  }

  .nav-brand__name-area {
    margin-right: 0px;
  }

  .navbar-menu.is-active {

  }
}
