.toolbar {
  color: #5d5d5d;
  align-items: center;
  background-color: #fafafa;
  box-shadow: 0 1px 0 #e1e1e1;
  display: flex;
  font-size: 0.75rem;
  height: var(--height-toolbar);
  justify-content: flex-start;
  position: sticky;
  top: var(--height-navbar);
  z-index: var(--z-index-toolbar);
}

.toolbar a,
.toolbar a:visited {
  color: inherit;
}

.nav-toggle {
  background: url(../img/menu.svg) no-repeat 50% 47.5%;
  background-size: 49%;
  border: none;
  outline: none;
  cursor: pointer;
  display: block;
  height: 2.5rem;
  padding: 0;
  width: 2.5rem;
  margin-right: -0.25rem;
}

.toc-toggle {
    background: url(../img/chevron.svg) no-repeat 50% 47.5%;
    background-size: 49%;
    border: none;
    outline: none;
    cursor: pointer;
    display: block;
    height: 2.5rem;
    padding: 0;
    width: 2.5rem;
    margin-right: -0.25rem;
}


.nav-toggle::-moz-focus-inner {
  border: none;
}

.toc-toggle::-moz-focus-inner {
  border: none;
}

@media screen and (min-width: 1024px) {
  .nav-toggle {
    display: none;
  }

  .toc-toggle {
    display: none;
  }
}

.nav-toggle.is-active {
  background-image: url(../img/back.svg);
  background-size: 41.5%;
}

.toc-toggle.is-active {
  background-size: 41.5%;
}

.home-link {
  background: url(../img/home-o.svg) no-repeat 50% 45%;
  background-size: 50%;
  display: block;
  height: 2.5rem;
  padding: 0;
  width: 2.5rem;
}

.home-link:hover,
.home-link.is-current {
  background-image: url(../img/home.svg);
}

.edit-this-page {
  display: none;
  padding-right: 0.5rem;
}

@media screen and (min-width: 1024px) {
  .edit-this-page {
    display: block;
  }
}

.toolbar .edit-this-page a {
  color: #8e8e8e;
}
